    align-items: center;
    justify-content: flex-end;
    transition: all .4s
}

.carousel .slick-slide {
    outline: none;
    border: none;
    transform: scale(.85);
    transition: all .7s
}

.carousel .slick-slide>div {
    width: 100%;
}

.tres-Item>img {
    max-width: 38%;
    position: relative;
    bottom: 4rem;
    transition: all .3s;
}

.tres-Name {
    padding: 1.5rem 2.3rem;
    background: #1d2936;
    transform: rotate(-4deg) translate(1rem, -4.25rem);
    color: #fff;
    font-size: 1.15rem;
    transition: all .3s
}

.carousel .slick-slide:not(.slick-center) .tres-Name {
    font-size: 1rem;
    transform: rotate(-4deg) translate(0rem, -3.25rem);
    padding: 1.5rem 1.7rem;
}

.carousel .slick-slide:not(.slick-center) .tres-Item>img {
    max-width: 40%;
    position: relative;
    bottom: 2.5rem;
}

.carousel .slick-slide.slick-center .tres-Item>img {
    max-width: 48%;
    position: relative;
    bottom: 2rem;
    transition: all .3s;
}

.mainSlider {
    position: relative;
}

.carousel .slick-slide {
    transition: all .4s ease-in-out;
}

.carousel .slick-track {
    top: 5rem;
}

.carousel .slick-current {
    transform: scale(1.2);
    transition: all .4s ease-in-out;
    z-index: 15;
    position: relative;
}

.sliderNavigation {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    bottom: 0;
    margin: auto;
    text-align: center;
}

.sliderNav button {
    border-radius: 50%;
    -webkit-appearance: none;
    border: none;
    outline: none;
    width: 3rem;
    height: 3rem;
    z-index: 10;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    position: relative;
    display: block;
    background: #ffdb35;
    background: linear-gradient(180deg, rgba(255, 220, 59, 1) 0%, rgba(254, 237, 124, 1) 100%);
    transform: none;
}

.sliderNav button::after {
    content: '';
    position: absolute;
    left: .32rem;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    -webkit-filter: drop-shadow(2px 4px 3px #ffdb35);
            filter: drop-shadow(2px 4px 3px #ffdb35);
    width: 19px;
    height: 19px;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,)
}

.sliderNav button.prev {
    transform: rotateY(180deg);
}

.sliderNav {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    width: 3.7rem;
    height: 3.7rem;
    top: 0;
    bottom: 0;
    margin: auto;
    border-radius: 50%;
    box-shadow: 1px 2px 6px gray;
    right: 0;
}

.sliderNav:first-child {
    right: auto;
    left: 0;
}

.carousel .slick-prev:before, .carousel .slick-next:before {
    display: none;
    content: '' !important;
}

.sliderNav button:hover, .carousel .slick-prev:hover, .carousel .slick-prev:focus, .carousel .slick-next:hover, .carousel .slick-next:focus {
    box-shadow: inset 5px 4px 20px #ffdb35;
    background: linear-gradient(180deg, rgba(255, 220, 59, 1) 0%, rgba(254, 237, 124, 1) 100%);
}

.sliderNav button:hover::after {
    -webkit-animation: movingRight .8s infinite linear;
            animation: movingRight .8s infinite linear
}

@-webkit-keyframes movingRight {
    0% {
        transform: translateX(-.3rem)
    }
    100% {
        transform: translateX(.3rem);
    }
}

@keyframes movingRight {
    0% {
        transform: translateX(-.3rem)
    }
    100% {
        transform: translateX(.3rem);
    }
}

.sliderNav.disabled, .sliderNav.disabled button {
    background: #f0f0f5;
    box-shadow: none;
}

.sliderNav.disabled {
    background: #c7c7c8;
    pointer-events: none;
}

.sliderNav.disabled button::after {
    box-shadow: none;
    border-top: .65rem solid transparent;
    border-bottom: .65rem solid transparent;
    border-left: .65rem solid #c7c7c8;
    -webkit-filter: none;
            filter: none
}

